package models

import kotlinx.serialization.json.JsonNames


data class Cursors (
    val after: String,
    val before: String
)

data class Item (
    @JsonNames
    val track: Track,

    @JsonNames("played_at", "playedAt")
    val playedAt: String,

    val context: Any? = null
)

data class Track (
    val album: Album,
    val artists: String,
    @JsonNames("duration_ms", "durationMS")
    val durationMS: Long,
    val explicit: Boolean,
    val href: String,
    val id: String,
    val name: String,
    val popularity: Long,
    @JsonNames("preview_url", "previewURL")
    val previewURL: String,
    val uri: String
){








    companion object {
        fun fromDynamic(inobj:dynamic) :Track{

            var artistsStr = ""
            for(i in 0..inobj.track.artists.length-1)
                artistsStr += inobj.track.artists[i].name.toString() + ", "

            return Track(
                inobj.track.album,
                artistsStr,
                inobj.track.duration_ms,
                inobj.track.explicit,
                inobj.track.href,
                inobj.track.id,
                inobj.track.name,
                inobj.track.popularity,
                inobj.track.preview_url,
                inobj.track.uri

            )
        }

        fun listFromDynamic(inListObj:dynamic) :List<Track>{
            val tracksout = mutableListOf<Track>()

            for(t in inListObj.items) {
                //console.log(t)
                val plkt = Track.fromDynamic(t)
                tracksout.add(plkt)
            }
            //        it -> trackout.add(Track.fromDynamic(it))

            //trackout.forEach { console.info(it) }

            return tracksout.toList<Track>()
        }
    }
}

data class Album (

    val artists: List<Artist>,
    val href: String,
    val id: String,
    val images: List<Image>,
    val name: String,
    val uri: String
)

data class Artist (
    val id: String,
    val name: String,
    val uri: String
)




