package pages

import dataListView
import Requester
import kotlinx.browser.document
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import kotlinx.html.*
import kotlinx.html.dom.append
import kotlinx.html.js.onClickFunction
import misc.Media

object PlaylistPage : InfoViewPage {
    override fun loadPage() {
        document.body?.append {

            GlobalScope.launch {





                val qPid = params.get("pid")
                if (qPid == null) {
                    console.log("no playlist id")
                }

                val playlist = Requester().getPlaylist(qPid.toString())
                console.log(playlist)
                playlist.tracksList = Requester().getPlaylistTracks(playlist.tracksLink)
                console.log(playlist.tracksList)

                div {

                    divInfoView(
                        imgUrl = playlist.imageUri,
                        imgTxt = playlist.name
                    ){
                            li {
                                +"playlist tracks: ${playlist.numTracks}"
                            }

                        //id bottom right
                        small {
                            style = "text-align: right; flex-grow: 1; float: right; margin-right: 2vw; "
                            +playlist.id
                        }

                        br { style = "break: both;" }
                    }

                    small {+"click to hear a preview!" }






                    val v = dataListView(
                        "playlist tracks",
                        playlist.tracksList
                    ) {cardData ->

                            onClickFunction = { ev ->
                                Media.playSoundFromUrl(cardData.previewURL)
                            }

                            p { +cardData.name }

                            p { +cardData.artists }


                    }.apply {

                    }


                }

            }

        }
    }


}
